import React, { Component } from 'react';
import './App.css';
import { ServerList } from './serverlist';
import { Form } from './form';


export class App extends Component {

  constructor(props) {
    super(props);
    // console.log(props);
    this.state = {
      serverName: null,
      servers: []
    };
    this.inputChange = this.inputChange.bind(this);
    this.addServer = this.addServer.bind(this);
  }

  //Assuming i click a button that trigger this method , Why is the console logs the server state empty even i set it equal to the copied servers which already hold have an item inside?
  addServer(e) {
    e.preventDefault();
    if (e.target.serverName.value.length === 0) {
          console.log('No server is added')
    } else {
      const servers = this.state.servers.slice();
      let rand = Math.floor(Math.random() * 20) < 10 ? 'Online' : 'Offline';
      servers.push({ 'serverName': this.state.serverName, status: rand });
      this.setState({ serverName: null });
      e.target.serverName.value = "";
      this.setState({ servers: servers });
    }
    
  }


  inputChange(event) {
    this.setState({ serverName: event.target.value })
  }



  render() {
    return (
      <div className="app">
        <div className="container control-box">
            <h4> Server Name : {this.state.serverName}</h4>
            <h4> Servers Count: {this.state.servers.length}</h4>
            <div className="control-bottom-ui">
            <form onSubmit={this.addServer}>
            <input name="serverName" onChange={this.inputChange} type="text" placeholder='Enter server name' ref={ node => (this.inputNode = node)}/>
            <button> Add </button>
            </form>
            </div>
        </div>
         <ServerList server={this.state.servers}/>
       </div>
    )
  }
}




export default App;



