import React from 'react';
import ReactDOM from 'react-dom';
import App from './App.js';
import PropTypes from 'prop-types';



export class ServerList extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            info: {
                serverName: null
            }
        }
    }

    render(){
    let server = this.props.server.map((item, index) => {
           let colorStatus = item.status == 'Online' ? 'online' : 'offline';
           return <li className={colorStatus} key={index} onClick={() => this.setState({info: {serverName: item.serverName}})}> {item.serverName} </li>;
        })
        return (
                <div className="container-server">
                <h1> {this.state.info.serverName} </h1>
                <ul> {server} </ul>
                </div>
        )
    }
}


ServerList.propTypes = {
  server: PropTypes.array
}


export default ServerList ;


