import React, { Component } from 'react';
import ReactDOM from 'react-dom';



export class Form extends Component {

    constructor(props){
        super(props);
        this.state = {
            title: '',
            error: null,
            errorOccured: false,
            errorHandler:  [
            {
                error: 'Title should be concise',
                passingLength: 10
            },
            {
                error: 'Message is not well defined',
                passingLength: 120
            }
                            ]
        }
        this.titleChange = this.titleChange.bind(this);
        this.messageChange = this.messageChange.bind(this);
    }
    titleChange(event) {
        this.setState({title: event.target.value});
        this.state.title.length < this.state.errorHandler[0].passingLength ? this.setState({error: this.state.errorHandler[0].error, errorOccured: true }) :  this.setState({error: null,errorOccured: false})
        
    }
    
    messageChange(event) {
        event.target.value.length < this.state.errorHandler[1].passingLength ? this.setState({error: this.state.errorHandler[1].error, errorOccured: true}) :  this.setState({error: null, errorOccured: false})
    }
    
    submitClear(event) {
        event.target.value = '';
    }
    

    render() {
        return (
                <form onSubmit={this.props.onSubmit.bind(this)}>
                    <h1> Title : {this.state.title} </h1>
                    <h4> {this.state.error} </h4>
                    <input onChange={this.titleChange} type="text" placeholder="name" name="title" />
                    <textarea name="message" onChange={this.messageChange}></textarea>
                    <button>Submit </button>
                </form>
        )
    }
}




